/* usage : 

    @media VARIABLE_NAME {
        // style in here 
    }
*/
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 1.5rem;
  background-color: #1f2632;
  color: #000000;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  height: 100vh;
}

main {
  display: flex;
  flex-flow: column nowrap;
  padding: 3rem;
  padding-bottom: 1rem;
  background-color: #323a49;
  border-radius: 15px;
  max-width: 540px;
  width: 100%;
  height: auto;
  margin: 0 auto;
  text-align: center;
  box-shadow: 0px 15px 51px -12px #00000080;
}
main > h1 {
  color: #52ffa8;
  font-size: 1rem;
  letter-spacing: 0.4rem;
}
main > h1 > span {
  color: #52ffa8;
}
main > p {
  font-size: 1.5rem;
}
main > .btndice {
  background-color: #52ffa8;
  background-image: url(../../images/icon-dice.svg);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  border-radius: 50px;
  border: none;
  position: relative;
  align-self: center;
  width: 64px;
  height: 64px;
  top: 3rem;
  color: transparent;
}
@media only screen and (min-width: 420px) {
  main > .btndice {
    transition: all 0.2s cubic-bezier(0.48, 2.43, 0.21, 0.46);
  }
  main > .btndice:active {
    width: 70px;
    height: 70px;
  }
}
main > .btndice:hover {
  cursor: pointer;
  box-shadow: 0px 0px 20px 5px #51f6a4;
  transform: rotate(45deg);
}
@media only screen and (max-width: 420px) {
  main > img {
    content: url(../../images/pattern-divider-mobile.svg);
    height: 1rem;
  }
}

* {
  font-family: "Manrope", sans-serif;
  color: #cee3e9;
}

p {
  font-size: 1.75rem;
}

h1, h2, h3 {
  margin-top: 0px;
}

h1 {
  text-transform: uppercase;
}